@font-face {
  font-family: 'SUITE-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/SUITE-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

*,
*:after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: 'SUITE-Regular';
  background: #283345;
}

.container {
  width: 300px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin: 5rem auto 1rem;
  color: #fff;
  letter-spacing: 3px;
}

.container input {
  display: block;
  width: 100%;
  height: 40px;
  margin-bottom: 10px;
  font-size: 1.5rem;
  outline: none;
  border: none;
  border-bottom: 2px solid #ccc;
  background: none;
  text-align: right;
  color: #fff;
}

.buttonbox {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 50px);
  grid-gap: 5px;
}

button {
  border: 1px solid rgba(0, 0, 0, 0.21);
  border-bottom-color: rgba(0, 0, 0, 0.34);
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.34) inset,
    0 2px 0 -1px rgba(0, 0, 0, 0.13),
    0 3px 0 -1px rgba(0, 0, 0, 0.08),
    0 3px 10px -1px rgba(0, 0, 0, 0.21);
  cursor: pointer;
  font-size: 1.1rem;
}

button:hover {
  top: 1px;
  border-color: rgba(0, 0, 0, 0.34) rgba(0, 0, 0, 0.21) rgba(0, 0, 0, 0.21);
  position: relative;
}

.button_total {
  grid-column: 3/5;
  grid-row: 5/6;
  color: orangered;
}

.button_color {
  color: orangered;
}